import React, { Component } from 'react';
import PropTypes from 'prop-types';
import GridElement from '../GridElement/GridElement';
import { GridElementDefinitions } from '../../Data/Grid';
import './MainPage.css';

export default class MainPage extends Component {
	render() {
		return (
			<div className="main-page page quint">
				{GridElementDefinitions.map((element, index) => (
					<GridElement
						key={`grid-element-${index}`}
						index={index}
						{...element}
					/>
				))}
			</div>
		);
	}
}



