import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './GridElement.css';

export default class GridElement extends Component {
	static propTypes = {
		area: PropTypes.string.isRequired,
		children: PropTypes.array.isRequired,
		style: PropTypes.object
	};

	render() {
		const { index, id, style, area, children, animateFrom } = this.props;
		const animateClass = animateFrom ? `animate--${animateFrom}` : null;

		const stagger = index * 50;

		return (
			<section
				id={id}
				className={`grid-element cubic ${animateClass}`}
				style={{
					...style,
					gridArea: area,
					transitionDelay: stagger + 'ms',
					transitionDuration: 3000 + stagger + 'ms'
				}}
			>
				{children}
			</section>
		);
	}
}



