import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './LandingPage.css';

export default class LandingPage extends Component {
	static propTypes = {
		history: PropTypes.object.isRequired
	};

	constructor(props) {
		super(props);
		this.handleClick = this._handleClick.bind(this);
	}

	_handleClick = (event) => {
		this.props.history.push('/0');
	};

	render() {
		return (
			<div className="landing-page page">
				<span onClick={this.handleClick} className="landing-page__button quint">
					<span className="landing-page__word quint">regret</span>
					<span className="landing-page__word quint">was</span>
					<span className="landing-page__word quint">my</span>
					<span className="landing-page__word quint">only</span>
				</span>
			</div>
		);
	}
}



